export default {
  name: 'search',
  data () {
    return {
      searchText: ''
    }
  },
  props:['placeholder'],
  onLoad(options) {
	  this.searchText = options.keywords
  },
  watch: {
    $route () {
      this.searchText = this.$route.query.keywords
    }
  },
  methods: {
    search () {
		uni.navigateTo({
		  url: `/pages/search/index?keyword=${this.searchText}`
		})
    }
  }
}
